import { mapGetters, mapActions } from 'vuex'

export default {
  layout: 'default',
  middleware: 'auth',
  fetch() {
    this.getBoxes()
  },
  data() {
    return {
      form: {
        boxName: '',
        colour: '#336699',
      },
      search: '',
      allCheck: false,
      selected: [],
      items: [],
    }
  },
  computed: {
    ...mapGetters({
      BOXES: 'boxes/BOXES',
    }),
  },
  methods: {
    ...mapActions({
      GET_BOXES: 'boxes/GET_BOXES',
    }),
    async getBoxes() {
      await this.GET_BOXES()
      this.items = this.BOXES.data
    },
    async createBox() {
      const formattedForm = {
        name: this.form.boxName,
        colour: this.form.colour,
      }
      try {
        await this.$axios.$post('/boxes', formattedForm)
        this.getBoxes()
        this.clear()
      } catch (e) {
        console.log('error')
      }
    },
    clear() {
      Object.assign(this.$data, this.$options.data())
    },
  },
}
